	width:100%;
	height:100%;
	height:100vh;
	min-height:100vh;
	max-width:none;
	margin-left:0
}
.switch {
	margin-bottom:1rem;
	outline:0;
	position:relative;
	-webkit-user-select:none;
	-moz-user-select:none;
	-ms-user-select:none;
	user-select:none;
	color:#fefefe;
	font-size:.875rem
}
.switch-input {
	opacity:0;
	position:absolute
}
.switch-paddle {
	background:#cacaca;
	cursor:pointer;
	position:relative;
	width:4rem;
	height:2rem;
	border-radius:0;
	color:inherit;
	font-weight:inherit
}
.has-tip,.title-bar-title,caption {
	font-weight:700
}
input+.switch-paddle {
	margin:0
}
.switch-paddle::after {
	background:#fefefe;
	content:'';
	position:absolute;
	height:1.5rem;
	left:.25rem;
	top:.25rem;
	width:1.5rem;
	-webkit-transform:translate3d(0,0,0);
	transform:translate3d(0,0,0)
}
.switch-active,.switch-inactive,.tooltip.left::before {
	-webkit-transform:translateY(-50%);
	-ms-transform:translateY(-50%)
}
input:checked~.switch-paddle {
	background:#2199e8
}
input:checked~.switch-paddle::after {
	left:2.25rem
}
[data-whatinput=mouse] input:focus~.switch-paddle {
	outline:0
}
.switch-active,.switch-inactive {
	position:absolute;
	top:50%;
	-webkit-transform:translateY(-50%);
	    -ms-transform:translateY(-50%);
	        transform:translateY(-50%)
}
.switch-active {
	left:8%;
	display:none
}
input:checked+label>.switch-active {
	display:block
}
.switch-inactive {
	right:15%
}
input:checked+label>.switch-inactive {
	display:none
}
.switch.tiny .switch-paddle {
	width:3rem;
	height:1.5rem;
	font-size:.625rem
}
.switch.tiny .switch-paddle::after {
	width:1rem;
	height:1rem
}
.switch.tiny input:checked~.switch-paddle:after {
	left:1.75rem
}
.switch.small .switch-paddle {
	width:3.5rem;
	height:1.75rem;
	font-size:.75rem
}
.switch.small .switch-paddle::after {
	width:1.25rem;
	height:1.25rem
}
.switch.small input:checked~.switch-paddle:after {
	left:2rem
}
.switch.large .switch-paddle {
	width:5rem;
	height:2.5rem;
	font-size:1rem
}
.switch.large .switch-paddle::after {
	width:2rem;
	height:2rem
}
.switch.large input:checked~.switch-paddle:after {
	left:2.75rem
}
table {
	border-collapse:collapse;
	border-spacing:0;
	margin-bottom:1rem;
	border-radius:0
}
tbody,tfoot,thead {
	border:1px solid #f1f1f1;
	background-color:#fefefe
}
tfoot,thead {
	background:#f8f8f8;
	color:#0a0a0a
}
tfoot tr,thead tr {
	background:0 0
}
tfoot td,tfoot th,thead td,thead th {
	padding:.5rem .625rem .625rem;
	font-weight:700;
	text-align:left
}
tbody tr:nth-child(even) {
	background-color:#f1f1f1
}
@media screen and (max-width:63.9375em) {
	table.stack tfoot,table.stack thead {
		display:none
	}
	table.stack td,table.stack th,table.stack tr {
		display:block
	}
	table.stack td {
		border-top:0
	}
}
.tabs,.tabs-content {
	border:1px solid #e6e6e6
}
table.scroll {
	display:block;
	width:100%;
	overflow-y:scroll
}
table.hover tr:hover {
	background-color:#f9f9f9
}
table.hover tr:nth-of-type(even):hover {
	background-color:#ececec
}
.tabs {
	margin:0;
	background:#fefefe
}
.tabs::after,.tabs::before {
	content:' ';
	display:table
}
.tabs.simple>li>a {
	padding:0
}
.tabs.simple>li>a:hover {
	background:0 0
}
.tabs.vertical>li {
	width:auto;
	float:none;
	display:block
}
.tabs-title,.title-bar-left {
	float:left
}
.tabs.primary {
	background:#2199e8
}
.tabs.primary>li>a {
	color:#fefefe
}
.tabs.primary>li>a:focus,.tabs.primary>li>a:hover {
	background:#1893e4
}
.tabs-title>a {
	display:block;
	padding:1.25rem 1.5rem;
	line-height:1;
	font-size:12px;
	color:#2199e8
}
.tabs-title>a:focus,.tabs-title>a:hover,.tabs-title>a[aria-selected=true] {
	background:#e6e6e6
}
.tabs-content {
	background:#fefefe;
	-webkit-transition:all .5s ease;
	-o-transition:all .5s ease;
	transition:all .5s ease;
	border-top:0
}
.tabs-content.vertical {
	border:1px solid #e6e6e6;
	border-left:0
}
.tabs-panel {
	display:none;
	padding:1rem
}
.tabs-panel.is-active {
	display:block
}
.thumbnail {
	border:4px solid #fefefe;
	-webkit-box-shadow:0 0 0 1px rgba(10,10,10,.2);
	        box-shadow:0 0 0 1px rgba(10,10,10,.2);
	display:inline-block;
	line-height:0;
	max-width:100%;
	-webkit-transition:-webkit-box-shadow .2s ease-out;
	transition:-webkit-box-shadow .2s ease-out;
	-o-transition:box-shadow .2s ease-out;
	transition:box-shadow .2s ease-out;
	transition:box-shadow .2s ease-out, -webkit-box-shadow .2s ease-out;
	border-radius:0;
	margin-bottom:1rem
}
.thumbnail:focus,.thumbnail:hover {
	-webkit-box-shadow:0 0 6px 1px rgba(33,153,232,.5);
	        box-shadow:0 0 6px 1px rgba(33,153,232,.5)
}
.title-bar {
	background:#0a0a0a;
	color:#fefefe;
	padding:.5rem
}
.title-bar::after,.title-bar::before {
	content:' ';
	display:table
}
.menu-icon.dark::after,.menu-icon::after {
	content:'';
	top:0;
	left:0
}
.menu-icon,.title-bar-title {
	display:inline-block;
	vertical-align:middle
}
.title-bar .menu-icon {
	margin-left:.25rem;
	margin-right:.5rem
}
.title-bar-right {
	float:right;
	text-align:right
}
.menu-icon {
	position:relative;
	cursor:pointer;
	width:20px;
	height:16px
}
.menu-icon::after {
	position:absolute;
	display:block;
	width:100%;
	height:2px;
	background:#fff;
	-webkit-box-shadow:0 7px 0 #fff,0 14px 0 #fff;
	        box-shadow:0 7px 0 #fff,0 14px 0 #fff
}
.menu-icon:hover::after {
	background:#cacaca;
	-webkit-box-shadow:0 7px 0 #cacaca,0 14px 0 #cacaca;
	        box-shadow:0 7px 0 #cacaca,0 14px 0 #cacaca
}
.menu-icon.dark {
	position:relative;
	display:inline-block;
	vertical-align:middle;
	cursor:pointer;
	width:20px;
	height:16px
}
.menu-icon.dark::after {
	position:absolute;
	display:block;
	width:100%;
	height:2px;
	background:#000;
	-webkit-box-shadow:0 7px 0 #000,0 14px 0 #000;
	        box-shadow:0 7px 0 #000,0 14px 0 #000
}
.menu-icon.dark:hover::after {
	background:#666;
	-webkit-box-shadow:0 7px 0 #666,0 14px 0 #666;
	        box-shadow:0 7px 0 #666,0 14px 0 #666
}
.has-tip {
	border-bottom:dotted 1px #8a8a8a;
	position:relative;
	display:inline-block
}
.tooltip.top::before,.tooltip::before {
	content:'';
	display:block;
	width:0;
	height:0
}
.has-tip:hover {
	cursor:help
}
.tooltip {
	background-color:#0a0a0a;
	color:#fefefe;
	font-size:80%;
	padding:.75rem;
	position:absolute;
	z-index:10;
	top:calc(100% + .6495rem);
	max-width:10rem!important;
	border-radius:0
}
.tooltip::before {
	border:.75rem inset;
	border-color:transparent transparent #0a0a0a;
	border-bottom-style:solid;
	bottom:100%;
	position:absolute;
	left:50%;
	-webkit-transform:translateX(-50%);
	-ms-transform:translateX(-50%);
	transform:translateX(-50%)
}
.tooltip.top::before {
	border:.75rem inset;
	border-color:#0a0a0a transparent transparent;
	border-top-style:solid;
	top:100%;
	bottom:auto
}
.tooltip.left::before,.tooltip.right::before {
	content:'';
	display:block;
	width:0;
	height:0;
	bottom:auto;
	top:50%
}
.tooltip.left::before {
	border:.75rem inset;
	border-color:transparent transparent transparent #0a0a0a;
	border-left-style:solid;
	left:100%;
	-webkit-transform:translateY(-50%);
	    -ms-transform:translateY(-50%);
	        transform:translateY(-50%)
}
.tooltip.right::before {
	border:.75rem inset;
	border-color:transparent #0a0a0a transparent transparent;
	border-right-style:solid;
	left:auto;
	right:100%;
	-webkit-transform:translateY(-50%);
	-ms-transform:translateY(-50%);
	transform:translateY(-50%)
}
.top-bar {
	padding:.5rem
}
.top-bar::after,.top-bar::before {
	content:' ';
	display:table
}
.top-bar,.top-bar ul {
	background-color:#eee
}
.top-bar input {
	width:200px;
	margin-right:1rem
}
@media screen and (min-width:40em) {
	.top-bar-left {
		float:left
	}
	.top-bar-right {
		float:right
	}
}



